.page-navigation {
    position: relative;

    &__wrapper {
        overflow: hidden;
        overscroll-behavior-x: contain;
        scroll-snap-type: x mandatory;
        scroll-behavior: smooth;

        border-bottom: 1px solid #ebebeb;

        @include hideScrollbar;
    }

    &__list {
        display: flex;
        padding-bottom: 1px;
    }

    &__controls {
        position: absolute;
        display: flex;
        width: 100%;
        height: 100%;
        left: 0;

        .control-btn {
            @include btn-reset;
            position: absolute;
            z-index: 2;
            width: 30px;
            height: 30px;
            top: 45%;
            transform: translateY(-50%);
            display: flex;
            justify-content: center;
            align-items: center;

            cursor: pointer;

            font-size: 0;

            background-color: rgba(#fff, 0.95);

            &.disabled {
                opacity: 0;
                pointer-events: none;
            }

            &::before {
                content: '';
                position: absolute;
                width: 9px;
                height: 9px;

                transform: rotate(135deg);

                border-width: 0 1px 1px 0;
                border-style: solid;
                border-color: $theme-color;
            }

            &.prev {
                left: 0;
            }

            &.next {
                right: 0;

                &::before {
                    transform: rotate(-45deg);
                }
            }
        }
    }

    &__link {
        flex: 0 0 auto;
        position: relative;
        padding: 12px 24px 20px;
        scroll-snap-align: start;

        outline: none;

        font-size: 14px;
        white-space: nowrap;
        font-weight: 500;
        color: $text-color;
        text-decoration: none;
        outline: none;

        transition: color 300ms ease;

        &:hover,
        &:active,
        &:focus {
            color: $theme-color;

            text-decoration: none;
        }

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 2px;
            bottom: -1px;
            left: 0;

            transform: scaleX(0);

            background-color: $theme-color;

            transition: transform 300ms ease;
        }

        &.active {
            font-weight: 600;

            &::before {
                transform: scaleX(1);
            }
        }

        svg {
            position: relative;
            width: 16px;
            height: 16px;
            right: 0px;
            top: 0;

            fill: $theme-color;
        }
    }
}

@media #{$tablet} {
    .page-navigation {
        &__item {
            padding-right: 16px;
        }

        &__link {
            font-size: 14px;
            padding: 12px 52px 20px;
        }
    }
}

@media #{$laptop} {
    .page-navigation {
        &__link {
            font-size: 16px;
        }
    }
}

@media #{$desktop} {
    .page-navigation {
        &__link {
            font-size: 18px;
        }
    }
}
